.slider {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
    display: flex;
    align-items: center;
    position: absolute;

    overflow: hidden;
}

.slide {
    min-width: 100%;
    height: 100vh;
    overflow: hidden;
    transition: 1.25s;
}

.slideImg{
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* filter: brightness(60%); */
}